"use client";
import { useState } from "react";
import Image from "next/image";

const newsPage = () => {
  const [searchTerm, setSearchTerm] = useState("");

  const newsList = [
    { id: 1, title: "New Student Orientation 2025", author: "Admin", image: "https://picsum.photos/400/250", date: "Jul 10, 2025" },
    { id: 2, title: "MPLS 2025 SMK Telkom Malang", author: "Fadi", image: "https://picsum.photos/400/251", date: "Jul 21, 2025" },
    { id: 3, title: "Graduation Day Celebration", author: "Rafa", image: "https://picsum.photos/400/252", date: "Aug 2, 2025" },
  ];

  // Filter news by title (case-insensitive)
  const filteredNews = newsList.filter((item) =>
    item.title.toLowerCase().includes(searchTerm.toLowerCase())
  );

  return (
    <div className="max-w-7xl mx-auto px-6 md:px-12 py-12">
      <h1 className="text-3xl font-semibold mb-8 text-gray-800">Berita Sekolah</h1>

      {/* üîç Search Box */}
      <div className="mb-8">
        <input
          type="text"
          placeholder="Cari berita..."
          className="w-full md:w-1/2 px-4 py-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-primary"
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
        />
      </div>

      {/* üì∞ News Grid */}
      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-8">
        {filteredNews.length > 0 ? (
          filteredNews.map((item) => (
            <div key={item.id} className="bg-white rounded-xl shadow hover:shadow-lg transition p-4">
              <Image
                src={item.image}
                alt={item.title}
                width={400}
                height={250}
                className="rounded-lg object-cover w-full mb-4"
              />
              <h2 className="text-xl font-semibold text-gray-800 mb-2">{item.title}</h2>
              <p className="text-gray-500 text-sm mb-1">{item.date}</p>
              <p className="text-gray-600 text-sm">By {item.author}</p>
            </div>
          ))
        ) : (
          <p className="text-gray-500">Berita tidak ditemukan üò¢</p>
        )}
      </div>
    </div>
  );
};

export default newsPage;
